import uniIcon from '../uni-icon/uni-icon.vue'
export default {
	name: 'UniRate',
	components: {
		uniIcon
	},
	props: {
		isFill: {
			// 星星的类型，是否镂空
			type: Boolean,
			default: true
		},
		color: {
			// 星星的颜色
			type: String,
			default: '#ececec'
		},
		activeColor: {
			// 星星选中状态颜色
			type: String,
			default: '#ffca3e'
		},
		size: {
			// 星星的大小
			type: [Number, String],
			default: 24
		},
		value: {
			// 当前评分
			type: [Number, String],
			default: 0
		},
		max: {
			// 最大评分
			type: [Number, String],
			default: 5
		},
		margin: {
			// 星星的间距
			type: [Number, String],
			default: 0
		},
		disabled: {
			// 是否可点击
			type: Boolean,
			default: false
		}
	},
	data() {
		return {
			valueSync: ''
		}
	},
	computed: {
		stars() {
			const value = Number(this.valueSync) ? Number(this.valueSync) : 0
			const starList = []
			const floorValue = Math.floor(value)
			const ceilValue = Math.ceil(value)
			for (let i = 0; i < this.max; i++) {
				if (floorValue > i) {
					starList.push({
						activeWitch: '100%'
					})
				} else if (ceilValue - 1 === i) {
					starList.push({
						activeWitch: (value - floorValue) * 100 + '%'
					})
				} else {
					starList.push({
						activeWitch: '0'
					})
				}
			}
			return starList
		}
	},
	created() {
		this.valueSync = this.value
	},
	methods: {
		_onClick(index) {
			if (this.disabled) {
				return
			}
			this.valueSync = index + 1
			this.$emit('change', {
				value: this.valueSync
			})
		}
	}
}
